// about

.about__content {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  width: 320px;
  min-height: 385px;
  .centered();
  color: @basic-white;

  @media (min-width: @tablet-width) {
    width: auto;
    min-height: 665px;
    margin-right: 0;
    margin-left: 0;
    padding-bottom: 60px;
    color: @basic-black;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    min-height: 690px;
    .centered();
    padding-left: 160px;
  }
}

.about__name {
  margin-bottom: 0.25em;
  font-size: 30px;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    margin-top: auto;
    margin-bottom: 0.7em;
    font-size: 42px;
  }

  @media (min-width: @desktop-ultra-w) {
    position: relative;
    font-size: 52px;

    &::before,
    &::after {
      content: "Online";
      position: absolute;
      z-index: -1;
      top: -50px;
      left: -60px;
      color: @basic-white;
      font-size: 96px;
      font-weight: 300;
      line-height: 1;
    }

    &::after {
      content: "workshop";
      top: auto;
      left: 130px;
      bottom: -20px;
      font-size: 77px;
    }
  }
}

.about__title {
  margin-bottom: 1.65em;
  font-weight: 300;

  @media (min-width: @tablet-width) {
    margin-bottom: 2.5em;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 2em;
    font-size: 18px;
  }
}

.about__contacts {
  display: none;
  .mp-reset();
  margin-top: auto;
  list-style: none;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 150px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 170px;
  }
}

.about__link {
  @media (min-width: @tablet-width) {
    color: currentColor;
    font-size: 12px;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    font-size: 14px;
  }
}
