// page-button

.page-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 168px;
  min-height: 40px;
  .mp-reset();
  padding: 7px 44px;
  color: currentColor;
  font-size: 14px;
  line-height: 1.28;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  border: none;

  @media (min-width: @tablet-width) {
    padding: 14px 26px;
    font-size: 20px;
  }
}
