// cat-fullname

.common-field {
  .mp-reset();
  border: none;
}

.common-field__content {
  .mp-reset();
  list-style: none;
}

.common-field__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 13px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 20px;
  }
}

.common-field__item--spec {
  display: block;
}

.common-field__label {
  margin-right: 16px;

  @media (min-width: @tablet-width) {
    margin-right: 17px;
  }
}

.common-field__label--spec {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 0;

  @media (min-width: @tablet-width) {
    margin-right: 0;
  }
}

.common-field__input {
  flex-grow: 1;
  padding-top: 13px;
  padding-right: 14px;
  padding-bottom: 11px;
  padding-left: 14px;
  text-transform: inherit;
  border: 1px solid @page-grey-l;

  @media (min-width: @tablet-width) {
    padding-top: 10px;
    padding-right: 18px;
    padding-bottom: 11px;
    padding-left: 18px;
  }

  &:hover {
    border-color: @page-grey-m;
  }

  &:active {
    border-color: @page-dark-grey;
  }

  &:focus {
    border-color: @page-dark-grey;
  }

  &:focus:invalid {
    border-color: @page-red;
  }

  &:focus:valid {
    border-color: @page-green;
  }

  &:focus:valid + .common-field__icon {
    fill: @page-green;
    stroke: @page-green;
  }

  &:focus:invalid + .common-field__icon {
    fill: @page-red;
    stroke: @page-red;
  }
}

.common-field__input--spec {
  flex-grow: 1;
  margin-left: 10px;
  padding-right: 34px;

  @media (min-width: @tablet-width) {
    margin-left: 16px;
  }
}

.common-field__icon {
  position: absolute;
  right: 14px;
  top: 50%;
  fill: @page-dark-grey;
  transform: translateY(-50%);

  &--mail {
    stroke: @page-dark-grey;
  }
}

.common-field__textarea {
  width: 100%;
  min-height: 150px;
  padding-top: 13px;
  padding-right: 12px;
  padding-bottom: 13px;
  padding-left: 14px;
  text-transform: inherit;
  border: 1px solid @page-grey-l;
  resize: none;

  @media (min-width: @tablet-width) {
    padding-top: 10px;
    padding-right: 19px;
    padding-bottom: 10px;
    padding-left: 19px;
  }
}
