// page-title

.page-title {
  color: @basic-black;
  font-size: 36px;
  line-height: 1;
  font-weight: 400;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    text-align: left;
  }
}

.page-title--sub {
  color: @basic-black;
  line-height: 1.11;
  text-align: left;

  @media (min-width: @tablet-width) {
    line-height: 1;
  }
}
