// custom-link

.custom-link {
  position: relative;
  width: 130px;
  padding-right: 0.25em;
  padding-bottom: 0.25em;
  color: currentColor;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.05em;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3px;
    background-color: currentColor;
    transition-property: bottom;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }

  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 105px;
    width: 0;
    height: 3px;
    background-color: currentColor;
    transition-property: width;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }

  &:hover::before {
    bottom: -2px;
  }

  &:hover::after {
    width: 25px;
  }

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 140px;
    font-size: 16px;

    &::after {
      top: 9px;
      left: 115px;
    }
  }
}
