// annotation-text

.annotation-text {
  margin-bottom: 1em;
  color: currentColor;
  font-size: 12px;
  font-weight: 400;
  line-height: 1;

  @media (min-width: @tablet-width) {
    margin-bottom: 0.5em;
  }

  @media (min-width: @desktop-ultra-w) {
    font-size: 14px;
  }
}

.annotation-text--inner {
  color: @basic-black;
  line-height: 1.28;
  text-align: left;

  @media (min-width: @tablet-width) {
    line-height: 1.5;
  }
}
