// fonts
@font-face {
  font-family: "Source-serif Pro";
  font-display: swap;
  font-style: normal;
  font-weight: 300;
  src: local("sourceserifpro300"),
    url("../fonts/sourceserifpro300.woff2") format("woff2"),
    local("source serif pro 300"),
    url("../fonts/sourceserifpro300.woff") format("woff");
}

@font-face {
  font-family: "Source-serif Pro";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("sourceserifpro400"),
    url("../fonts/sourceserifpro400.woff2") format("woff2"),
    local("source serif pro 400"),
    url("../fonts/sourceserifpro400.woff") format("woff");
