// work-gallery

.work-gallery {
  position: relative;
  margin-bottom: 20px;
  // overflow: hidden;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}

.work-gallery--dried {
  @media (min-width: @tablet-width) {
    width: 37%;
    // max-width: 240px;
    margin-top: 120px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 414px;
  }
}

.work-gallery--compose {
  @media (min-width: @tablet-width) {
    align-self: flex-start;
    width: 28.3%;
    // width: 184px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 317px;

    .work-gallery__desc {
      max-height: 80px;
    }
  }
}

.work-gallery--eatable {
  @media (min-width: @tablet-width) {
    width: 28.3%;
    // width: 184px;
    margin-top: 140px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 317px;
  }
}

.work-gallery--regular {
  @media (min-width: @tablet-width) {
    width: 28.3%;
    // width: 184px;
    margin-top: -170px;
    margin-left: 40%;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 317px;
    margin-top: -240px;
  }
}

// * photo count

.work-gallery__photo-count {
  position: absolute;
  z-index: 101;
  top: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  padding: 0.4em;
  color: @basic-white;
  font-family: "Arial", sans-serif;
  font-size: 13px;
  line-height: 1;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.5);
}

.work-gallery__carousel {
  .mp-reset();
  list-style: none;
  overflow: hidden;

  li {
    display: none;
    min-width: 100%;
  }

  .work-gallery__active {
    display: block;
  }
}

.work-gallery__img {
  img {
    width: 100%;
  }
}

.work-gallery__desc {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 700;

  @media (min-width: @tablet-width) {
    font-size: 16px;
  }
}

// * controls

.work-gallery__controls {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  .mp-reset();
  list-style: none;
  margin-bottom: 10px;
}

.work-gallery__control {
  position: relative;
  width: 8px;
  height: 8px;
  .mp-reset();
  background-color: @basic-light;
  border: none;
  border-radius: 100%;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    background-color: transparent;
    transform: translate(-50%, -50%);
  }
}

.work-gallery__control--current {
  background-color: @page-red;
}
