// section-bullet

.section-bullet {
  position: absolute;
  display: none;

  @media (min-width: @desktop-ultra-w) {
    top: 23%;
    left: 20px;
    display: block;
    padding: 0.5em;
    color: currentColor;
    font-size: 20px;
    font-style: italic;
    letter-spacing: 0.1em;
    font-weight: normal;
    text-decoration: none;
    transform: translateY(-50%);

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: currentColor;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 50%;
      height: 1px;
      box-shadow: 0 100px 0 0 currentColor, 0 150px 0 0 currentColor,
        0 200px 0 0 currentColor, 0 250px 0 0 currentColor;
    }
  }
}

.section-bullet--center {
  @media (min-width: @desktop-ultra-w) {
    top: 50%;
  }
}
