// page

.page__body {
  position: relative;
  background-color: @page-gray;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    width: 320px;
    height: 475px;
    background-color: @page-gray;
    background-image: url("../img/index-background-mobile@1x.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    transform: translateX(-50%);
  }

  @media (min-width: @tablet-width) {
    &::before {
      left: auto;
      right: 0;
      width: 52%;
      height: 825px;
      background-image: url("../img/index-background-desktop@1x.png");
      background-size: cover;
      background-position: top left;
      transform: translateX(0);

      @media (min-resolution: 1.5dppx), (min-resolution: 144dpi) {
        background-image: url("../img/index-background-desktop@2x.png");
      }
    }
  }

  @media (min-width: @desktop-ultra-w) {
    &::before {
      width: 48%;
      height: 865px;
      // background-position: center;
      background-size: contain;
    }
  }
}

.page__main {
  overflow: hidden;
}
