// page-subtitle

.page-subtitle {
  position: relative;
  font-size: 18px;
  line-height: 1;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;

  &::before,
  &::after {
    content: "Works";
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    color: @basic-white;
    font-size: 60px;
    font-weight: 300;
    letter-spacing: 0.1em;
    transform: translate(-50%, -50%);
  }

  &::after {
    content: "";
    top: -60px;
    left: calc(50% - 25px);
    width: 50px;
    height: 2px;
    background-color: @page-red;
    transform: rotate(90deg);
  }

  @media (min-width: @tablet-width) {
    font-size: 24px;

    &::before {
      font-size: 108px;
    }

    &::after {
      top: -100px;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    font-size: 35px;

    &::after {
      top: -130px;
    }
  }
}
