// portfolio

.portfolio__content {
  padding-top: 141px;
  padding-bottom: 35px;

  @media (min-width: @tablet-width) {
    padding-top: 255px;
    padding-bottom: 145px;
  }

  @media (min-width: @desktop-ultra-w) {
    position: relative;
    width: @desktop-ultra-w;
    .centered();
  }
}

.portfolio__title {
  margin-bottom: 3em;
}

.portfolio__works {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  .mp-reset();
  list-style: none;
}
