// custom-elem
.custom-elem__content {
  .mp-reset();
  list-style: none;
}

.custom-elem__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 30px;
  margin-bottom: 13px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    min-height: 40px;
  }
}

// ! custom element base styles

.custom-elem__label {
  position: relative;
  padding-left: 45px;
  user-select: none;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 30px;
    height: 30px;
    border: 1px solid @page-grey-l;
  }

  &::after {
    content: "";
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    width: 13.5px;
    height: 13.5px;
    background-color: @page-green;
  }

  &:hover::before {
    border-color: @page-grey-m;
  }

  &:active::before,
  &:active::after {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    padding-left: 59px;

    &::before {
      width: 40px;
      height: 40px;
    }

    &::after {
      width: 18px;
      height: 18px;
    }
  }
}

// ! radio

.custom-elem__label--radio {
  &::before {
    top: -5px;
    border-radius: 100%;
  }

  &::after {
    top: 50%;
    left: 8px;
    border-radius: 100%;
    transform: translateY(-50%);
  }

  @media (min-width: @tablet-width) {
    &::before {
      top: -7px;
    }

    &::after {
      left: 11px;
    }
  }
}

// ! checkbox

.custom-elem__label--checkbox {
  &::before {
    top: -5px;
  }

  &::after {
    top: 50%;
    left: 6px;
    width: 18px;
    height: 10px;
    background-color: transparent;
    border: 2px solid @page-green;
    border-top: none;
    border-right: none;
    transform: translateY(-75%) rotate(-45deg);
  }

  @media (min-width: @tablet-width) {
    &::after {
      left: 8px;
      width: 22px;
      height: 14px;
    }
  }
}

// ! custom element states

.custom-elem__input:checked ~ .custom-elem__label::after {
  display: block;
}

.custom-elem__input:disabled ~ .custom-elem__label::before,
.custom-elem__input:disabled ~ .custom-elem__label::after {
  opacity: 0.6;
}
