// _navigation

._navigation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

// ! states

._navigation--nojs {
  ._burger {
    display: none;
  }

  ._site-list {
    display: flex;
  }
}
// * ie fallback
._burger {
  background-image: url("../img/icon-menu.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}

._navigation--active {
  ._burger {
    background-image: url("../img/icon-cross.svg");
  }

  ._burger__icon-menu {
    display: none;
  }

  ._burger__icon-cross {
    display: block;
  }

  ._site-list {
    display: flex;
  }

  @media (min-width: @tablet-width) {
    ._burger {
      position: absolute;
      top: 50%;
      right: 40px;
      transform: translateY(-50%);
    }
  }
}

._burger__icon-cross {
  display: none;
}
