// header

.header__content {
  width: 320px;
  .centered();
  padding: 0;

  @media (min-width: @tablet-width) {
    width: 100%;
    margin-right: 0;
    margin-left: 0;
    padding: 0;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 20px;
    padding-left: 80px;
  }
}

.header__navigation {
  position: relative;
  width: 100%;
  padding-top: 10px;
  padding-right: 15px;
  padding-left: 15px;

  @media (min-width: @tablet-width) {
    padding-top: 20px;
    padding-right: 40px;
    padding-bottom: 20px;
    padding-left: 40px;
  }

  @media (min-width: @desktop-ultra-w) {
    padding: 0;
  }
}

.header__logo {
  z-index: 101;
  padding: 1px;
  border: 2px solid @page-pink;
  border-radius: 100%;
  width: 80px;
  height: 80px;

  @media (min-width: @tablet-width) {
    width: 120px;
    height: 120px;
    padding: 3px;
    border-width: 3px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 160px;
    height: 160px;
  }
}

.header__logo-img {
  width: 100%;
}

.header__burger {
  z-index: 101;

  @media (min-width: @tablet-width) {
    width: 40px;
    height: 40px;
    margin-left: auto;
  }

  @media (min-width: @desktop-ultra-w) {
    display: none;
  }
}

.header__icon {
  ._burger__icon-menu {
    fill: @basic-white;
  }

  @media (min-width: @tablet-width) {
    ._burger__icon-menu {
      fill: @basic-black;
    }
  }
}

// * basic nav

.header__site-list {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 475px;
  padding-top: 160px;
  padding-bottom: 32px;
  background-color: @basic-white;

  @media (min-width: @tablet-width) {
    min-height: 825px;
    padding-top: 240px;
  }

  @media (min-width: @desktop-ultra-w) {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    width: 460px;
    min-height: auto;
    margin-left: 70px;
    padding: 0;
    background-color: transparent;

    li:first-child {
      display: none;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      top: 52%;
      width: 100%;
      height: 5px;
      background-color: @basic-white;
      transform: translateY(-50%);
    }
  }
}

.header__site-link {
  padding-top: 0.7em;
  padding-right: 0.2em;
  padding-left: 0.2em;
  font-size: 22px;
  font-weight: 700;
  border-bottom: 4px solid @page-red;

  @media (min-width: @tablet-width) {
    font-size: 36px;
  }

  @media (min-width: @desktop-ultra-w) {
    padding: 0;
    font-size: 18px;
    font-weight: 400;
    border-bottom: none;
  }

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

// * phone list

.header__site-info {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    .mp-reset();
    margin-left: auto;
    margin-right: 60px;
    list-style: none;

    .header__site-link {
      padding: 0;
      font-size: 22px;
      color: @basic-white;
      text-decoration: none;
      border: none;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    margin-right: 240px;

    .header__site-link {
      font-size: 22px;
    }
  }
}
